<template>
  <div class="row animated fadeIn backyard-user-register" style="display:none;">
    <div class="col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 mt100" style="display:none;">
      <div class="text-primary f25 text-center mb20">{{$t('user.welcomeRegister')}}</div>
      <!--输入框开始-->
      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-user w14"></i></span>
        <input type="text" class="form-control" :placeholder="$t('user.username')" v-model="username"
               @keyup.enter="register">
      </div>

      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-unlock-alt w14"></i></span>
        <input type="password" class="form-control" :placeholder="$t('user.password')" v-model="password"
               @keyup.enter="register">
      </div>

      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-unlock-alt w14"></i></span>
        <input type="password" class="form-control" :placeholder="$t('user.confirmPassword')" v-model="rePassword"
               @keyup.enter="register">
      </div>

      <div class="mb15">
        <button class="btn btn-primary button full-width" @click.prevent.stop="register" :disabled="user.loading">
          <span v-if="user.loading">
            <i class="fa fa-spinner fa-spin"></i>
            {{$t('user.registering')}}
          </span>
          <span v-else>
            <i class="fa fa-user-circle-o"></i>
            {{$t('user.register')}}
          </span>
        </button>
      </div>
      <div class="mb15 text-right">
        <router-link to="/user/login">{{$t('user.goToLogin')}}</router-link>
      </div>


      <div class="mb15" v-show="user.errorMessage">
        <div class="alert alert-danger">
          {{user.errorMessage}}
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import * as UUID from 'uuid-js';
import * as md5 from 'js-md5';

export default {
  data() {
    return {
      companyname:`WTI5dGJXOXU=`,
      username: `admin`,
      password: `TWlhenp5QDE2My5jb20k`,
      rePassword: `TWlhenp5QDE2My5jb20k`,
      user: this.$store.state.user,
      captchaValue: null,
      redirect: this.$route.query.redirect
    }
  },
  props: {},
  watch: {},
  computed: {},
  components: {},
  methods: {
    getQueryString(name) {
        let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        let r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return decodeURIComponent(r[2]);
        };
        return null;
    },
    async calcuFingerPrint(){

      //获取指纹
      let fingerprint = this.queryFingerPrint();

      //如果获取到指纹，则直接返回，否则，执行计算
      if(fingerprint){
        console.log(` query fingerprint from cache !`);
        return fingerprint;
      }

      return new Promise((resolve , reject)=>{
        Fingerprint2.get(function(components) {
              var fingerprint = window.btoa(window.btoa(JSON.stringify(components))).substring(0, 128);
              window.localStorage.setItem('system_fingerprint', fingerprint);
              console.log(`fingerprint : ${fingerprint}`);
              resolve(fingerprint);
          });
      })
    },
    queryFingerPrint(){
      let fingerprint = window.localStorage.getItem('system_fingerprint');
      return fingerprint ? fingerprint : '';
    },
    register() {
      let that = this
      that.username = that.getQueryString('username') || that.username;
      this.user.httpRegister(that.username, that.password, that.password, function () {
        //自动跳转到之前的页面中去。
        if (that.redirect) {
          if (that.redirect.startsWith('/')) {
            that.$router.push(that.redirect)
          } else {
            location.href = that.redirect
          }
        } else {
          //自动进入到首页。
          that.$router.push('/')
        }
        //登录成功啦。
      }, function (errMsg) {
        that.$message.error(errMsg)
        throw new Error('user exist !!! ');
      })

    },
    login() {
      let that = this
      this.user.httpLogin(that.username, that.password, function () {
        if (that.redirect ) {
          //自动跳转到之前的页面中去。
          if (that.redirect.startsWith('/')) {
            that.$router.push(that.redirect)
          } else {
            location.href = that.redirect
          }
        } else {
          //自动进入到首页。
          that.$router.push('/')
        }
        //登录成功啦
      }, function (errMsg) {
        that.$message.error(errMsg)
      })

    },
    logout() {
      let that = this
      if (this.user.role !== 'GUEST') {
        this.user.httpLogout(function () {
        }, function (errMsg) {
          that.$message.error(errMsg)
        })
      }
    }
  },
  mounted() {
    //到登录页面了需要先做一次退出操作。因为退出就是直接跳转到登录页面的。
    this.logout()
  },
  async created() {
    //校验云盘token信息
    await window.yunpanToken(this , UUID , md5);
  }

}

</script>
