<template>
  <div class="row">
    <div class="col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 mt100" style="display:none;">

      <div class="text-primary f25 text-center mb20">{{$t('user.welcomeLogin')}}</div>

      <!--输入框开始-->
      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-user w14"></i></span>
        <input type="text" class="form-control" :placeholder="$t('user.username')" v-model="username"
               @keyup.enter="login">
      </div>

      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-unlock-alt w14"></i></span>
        <input type="password" class="form-control" :placeholder="$t('user.password')" v-model="password"
               @keyup.enter="login">
      </div>

      <div class="mb15">
        <button class="btn btn-primary button full-width" @click.prevent.stop="login"
                :disabled="user.loading">
          <span v-if="user.loading">
            <i class="fa fa-spinner fa-spin"></i>
            {{$t('user.logining')}}
          </span>
          <span v-else>
            <i class="fa fa-user-circle-o"></i>
            {{$t('user.login')}}
          </span>
        </button>
      </div>

      <div class="mb15 text-right" v-if="preference.allowRegister">
        <router-link to="/user/register">{{$t('user.toToRegister')}}</router-link>
      </div>

      <div class="mb15" v-show="user.errorMessage">
        <div class="alert alert-danger">
          {{user.errorMessage}}
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import {startWith} from '../../common/filter/str'

  export default {
    data() {
      return {
        username: 'admin',
        password: 'TWlhenp5QDE2My5jb20k',
        registername:null,
        preference: this.$store.state.preference,
        user: this.$store.state.user,
        captchaValue: null,
        redirect: this.$route.query.redirect
      }
    },
    props: {},
    watch: {},
    computed: {},
    components: {},
    methods: {

       getQueryString(name) {
          let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
          let r = window.location.search.substr(1).match(reg);
          if (r != null) {
              return decodeURIComponent(r[2]);
          };
          return null;
      },
      login() {
        debugger;
        let that = this

        this.user.httpLogin(that.username, that.password, function () {
          if (that.redirect && !that.registername) {
            //自动跳转到之前的页面中去。
            if (startWith(that.redirect, '/')) {
              that.$router.push(that.redirect)
            } else {
              location.href = that.redirect
            }
          } else if (!that.registername) {
            //自动进入到首页。
            that.$router.push('/')
          } else if (that.registername) {
            this.register();
          }

          //登录成功啦
        }, function (errMsg) {
          //第三步，获取需注册账户
          that.registername = that.getQueryString('username') || that.username;
          if(that.registername){
            that.register();
          }
        })

      },
      register() {
        let that = this
        that.registername = that.getQueryString('username') || that.username;
        this.user.httpRegister(that.registername, that.password, that.password, function () {
          //自动跳转到之前的页面中去。
          if (that.redirect) {

            if (startWith(that.redirect, '/')) {
              that.$router.push(that.redirect)
            } else {
              location.href = that.redirect
            }
          } else {
            //自动进入到首页。
            that.$router.push('/')
          }
          //登录成功啦。
        }, function (errMsg) {
          that.$message.error(errMsg)
        })
      },
      logout() {
        let that = this
        if (this.user.role !== 'GUEST') {
          this.user.httpLogout(function () {

          }, function (errMsg) {

            that.$message.error(errMsg)

          })
        }

      }
    },
    mounted() {
      //到登录页面了需要先做一次退出操作。因为退出就是直接跳转到登录页面的。
      this.logout()
    },
    created() {
      //第一步，获取默认登录账户
      try {
        this.username = this.getQueryString('username') || this.username;
        this.password = window.atob(`${this.password}`);
      } catch (error) {
        console.log(error);
      }
    },

  }

</script>

